// Colors
$color-light-grey-1: #ccc;
$color-light-grey-2: #e6e6e6;
$color-light-grey-3: #eee;
$color-medium-grey: #999;
$color-dark-grey-1: #333;
$color-dark-grey-2: #222;
$color-red: #e54e4b;
$color-white: #fff;
$color-black: #000;
$color-blue: #0081c2;

// Colors with opacity.
$color-black-10-op: rgba(0, 0, 0, 0.1);
$color-black-20-op: rgba(0, 0, 0, 0.2);
$color-black-25-op: rgba(0, 0, 0, 0.25);
$color-white-10-op: rgba(255, 255, 255, 0.1);
$color-white-75-op: rgba(255, 255, 255, 0.075);

$dropdown-width: 400px;

// The wrapper around everything.
.tbm {
  background-color: $color-black;
  position: relative;

  // Admins can add font awesome icons to menu items.
  [class^='fa-'],
  [class*=' fa-'] {
    margin-right: 5px;
  }

  .caret {
    display: inline-block;
    width: 0;
    height: 0;
    vertical-align: top;
    border-top: 4px solid $color-white;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent;
    content: '';
  }

  .hidden {
    display: none !important;
    visibility: hidden;
  }

  // Any <li> that has been designated as a "group" item (the content within
  // is not contained within a dropdown/submenu, but is instead always visible.

  // The title of a .mega-group.
  .mega-group-title {
    background: inherit;
    font-weight: bold;
    padding: 0 0 0 10px;
    margin: 0;
    text-transform: uppercase;

    &:hover,
    &:focus,
    &:active {
      background: inherit;
      color: inherit;
    }
  }

  // The container around the content within a .mega-group. Immediately follows
  // the .mega-group-title.
  .mega-group-ct {
    margin: 0;
    padding: 0;

    > .tbm-row {
      > [class*='span'] {
        > .tbm-column-inner {
          padding: 0;
        }
      }
    }
  }

  // The caption that can appear below any <li>.
  .mega-caption {
    display: block;
    white-space: nowrap;
    color: $color-medium-grey;
    font-size: 12px;
    margin-top: 3px;
    font-weight: normal;
  }
}

// The <ul> around the top level menu.
.tbm-nav {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin: 0;
  padding: 0;

  // <nolink> menu items.
  span.tbm-no-link {
    // When a <nolink> menu item has no submenus.
    &.tbm-no-submenu {
      &:hover {
        color: inherit !important;
      }
    }

    &:hover {
      cursor: default;
    }
  }
}

// Set up rowns and columns.
.tbm-row {
  column-gap: 10px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);

  [class*='span'] {
    min-height: 30px;
  }

  @for $i from 1 through 12 {
    .span#{$i} {
      grid-column-start: span #{$i};
    }
  }

  + .tbm-row {
    margin-top: 10px;
  }
}

// Top level menu items.
.tbm-item.level-1 {
  position: relative;

  > a,
  > span.tbm-no-link {
    border: 0;
    box-sizing: border-box;
    color: $color-white;
    display: block;
    font-weight: bold;
    height: 100%;
    padding: 15px 20px;
    text-shadow: none;

    &:focus,
    &:hover {
      background-color: $color-white;
      color: $color-red;
      text-decoration: none;
      text-shadow: none;
      outline: none;
    }

    > img {
      max-width: none;
    }
  }
}

// The <a> or <span> tag for any menu item that has a submenu.
.tbm-toggle {
  .caret {
    margin-left: 5px;
    margin-top: 8px;
  }

  &:active {
    outline: 0;
  }

  &:hover,
  &:focus {
    .caret {
      border-top-color: $color-red;
      border-bottom-color: $color-red;
    }
  }
}

// Top level only menu items that hav submenus.
.tbm-item--has-dropdown {
  // Items that are open (ie, submenu is visible).
  &.open {
    .caret {
      border-bottom-color: $color-red;
      border-top-color: $color-red;
    }

    .tbm-toggle {
      outline: 0;
    }

    // Change the color of the immediate toggle, but not any further down the
    // chain.
    > .tbm-toggle {
      background-color: $color-white;
      color: $color-red;
    }

    > .tbm-submenu {
      display: block;
      overflow: visible;
      clip: auto;
      height: auto;
    }
  }

  // Items that are active or open and active.
  &.active,
  &.open.active {
    > a,
    > a:hover,
    > a:focus,
    > span.tbm-no-link,
    > span.tbm-no-link:hover,
    > span.tbm-no-link:focus {
      background-color: $color-red;
      color: $color-white;
    }

    > a {
      &:hover,
      &:focus {
        cursor: pointer;
      }

      .mega-caption {
        color: $color-white;
      }
    }

    .caret {
      border-top-color: $color-white;
      border-bottom-color: $color-white;
      opacity: 1;
    }
  }
}

// The <div> around any submenu dropdown. This is a direct descendent child of
// the <li>.
.tbm-submenu {
  background-clip: padding-box;
  background-color: $color-white;
  border-radius: 0;
  border-top-width: 0;
  border: 1px solid $color-black-20-op;
  box-shadow: 0 5px 10px $color-black-20-op;
  display: none;
  left: 0;
  width: $dropdown-width;
  padding: 10px;
  position: absolute;
  top: 100%;
  z-index: 1000;

  .tbm-submenu {
    // Anything other than the 1st dropdown menu needs the top border.
    border-top-width: 1px;
  }

  li {
    > a,
    > span.tbm-no-link {
      border-bottom: 0;
      border-top: 1px solid $color-light-grey-3;
      color: $color-dark-grey-1;
      display: block;
      font-weight: normal;
      line-height: 20px;
      padding: 3px 20px;
      text-shadow: none;
      white-space: nowrap;

      &:hover,
      &:focus {
        text-decoration: none;
        color: $color-red;
        background-color: inherit;
        background-image: none;
      }
    }

    &:first-child > a,
    &:first-child > span.tbm-no-link {
      border-top: 0;
    }
  }

  // Do not display the down carat in any dropdowns.
  .caret {
    display: none;
  }

  > .active {
    > a,
    > span.tbm-no-link {
      color: $color-white;
      text-decoration: none;
      outline: 0;
      background-color: $color-blue;

      &:hover,
      &:focus {
        color: $color-white;
        text-decoration: none;
        outline: 0;
        background-color: $color-blue;
      }
    }
  }
}

// Any <li> that has sub-links, other than at the top level.
.tbm-item--has-flyout {
  position: relative;

  > a,
  > span.tbm-no-link {
    &:after {
      display: block;
      content: '';
      float: right;
      width: 0;
      height: 0;
      border-color: transparent;
      border-style: solid;
      border-width: 5px 0 5px 5px;
      border-left-color: $color-light-grey-1;
      margin: 5px 0 0;
    }
  }

  &:hover,
  &:focus {
    > a,
    > span.tbm-no-link {
      text-decoration: none;
      color: $color-red;
      background-color: inherit;
      background-image: none;

      &:after {
        border-left-color: $color-light-grey-3;
      }
    }
  }

  > .tbm-submenu {
    top: 0;
    left: 100%;
    margin-top: -6px;
    margin-left: -1px;
    border-radius: 0;
  }

  &:hover,
  &:focus,
  &.open {
    > .tbm-submenu {
      display: block;
      overflow: visible;
      clip: auto;
      height: auto;
    }
  }
}

// Any <ul> other than the top level.
.tbm-subnav {
  margin: 0;
  padding: 0;
  list-style: none;

  > li {
    display: block;
    list-style: none;

    &:first-child {
      > a,
      > span.tbm-no-link {
        margin-top: 0;
        padding-top: 0;
      }
    }

    a,
    span.tbm-no-link {
      padding: 5px 0;
      white-space: normal;
    }
  }
}

// Alignment options for dropdowns.
.tbm-left {
  > .tbm-submenu {
    left: 0;
  }
}

.tbm-right {
  > .tbm-submenu {
    left: auto;
    right: 0;
  }
}

.tbm-center {
  > .tbm-submenu {
    left: 50%;
    transform: translate(-50%);
  }
}

.tbm-item--has-flyout {
  &.tbm-left {
    > .tbm-submenu {
      left: 100%;
    }
  }

  .tbm-right {
    > .tbm-submenu {
      left: auto;
      right: 100%;
    }
  }
}

.tbm-justify {
  position: static;

  > .tbm-submenu {
    left: 0;
    margin-left: 0;
    top: auto;
  }

  > .tbm-submenu.span12 {
    width: 100%;
  }
}

// Any block that is placed in a mega menu.
.tbm-block {
  .block-title {
    font-size: 12px;
    color: $color-dark-grey-1;
    display: block;
    line-height: 20px;
    margin: 0 0 15px;
    text-transform: uppercase;
  }
}

// The mobile show/hide button.
.tbm-button {
  display: none;
  padding: 10px 14px;
  margin: 0;
  color: $color-white;
  text-shadow: 0 -1px 0 $color-black-25-op;
  background-color: $color-light-grey-2;
  box-shadow: inset 0 1px 0 $color-white-10-op, 0 1px 0 $color-white-75-op;

  &:hover,
  &:focus,
  &:active,
  &.active,
  &.disabled,
  &[disabled] {
    color: $color-white;
    background-color: $color-light-grey-1;
  }

  [class^='fa-'],
  [class*=' fa-'] {
    color: $color-dark-grey-1;
    margin: 0;
    text-shadow: none;
  }
}

@media (max-width: 979px) {
  .tbm {
    background-color: inherit;
    height: auto;

    .always-show {
      .tbm-item {
        > .tbm-submenu {
          display: block !important;
        }
      }

      .tbm-submenu {
        display: block !important;
      }

      .caret {
        display: none !important;
      }

      .tbm-item--has-flyout {
        > a,
        > span.tbm-no-link {
          &:after {
            display: none !important;
          }
        }
      }
    }

    .hidden-collapse {
      display: none !important;
    }

    .sub-hidden-collapse {
      > .nav-child {
        display: none !important;
      }

      .caret {
        display: none !important;
      }

      > a {
        &:after {
          display: none !important;
        }
      }
    }

    .tbm-row {
      width: 100% !important;
      min-width: 100% !important;
      left: 0 !important;
      margin-left: 0 !important;
      transform: none !important;

      [class*='span'] {
        width: 100% !important;
        min-width: 100% !important;
        left: 0 !important;
        margin-left: 0 !important;
        transform: none !important;
      }
    }

    .tbm-button {
      display: block;
      margin-bottom: 10px;

      &:hover {
        cursor: pointer;
      }
    }

    .tbm-collapse {
      background: $color-black;
      height: 0;
      overflow: hidden;
      position: absolute;
      top: 100%;
      width: 100%;
      z-index: 999;

      .tbm-nav {
        flex-direction: column;

        > li {
          > a,
          > span.tbm-no-link {
            border-bottom: 1px solid $color-dark-grey-2;
            border-radius: 0;
            border-right: 0;
            color: $color-white;
            font-size: 15px;
            margin-bottom: 0;
            text-shadow: none;
            font-weight: bold;
            padding: 9px 15px;

            &:hover,
            &:focus {
              background-color: $color-white;
              color: $color-red;
            }
          }

          &.active {
            > a,
            > span.tbm-no-link {
              &:hover,
              &:focus {
                background-color: $color-red;
              }
            }
          }
        }
      }

      .tbm-submenu {
        position: static;
        top: auto;
        left: auto;
        float: none;
        display: none;
        max-width: none;
        margin: 0;
        padding: 0;
        background-color: $color-black;
        border: 0;
        border-radius: 0;
        box-shadow: none;
        width: 100% !important;

        a,
        span.tbm-no-link {
          border-bottom: 1px solid $color-dark-grey-2;
          border-right: 0;
          border-top: 1px solid $color-dark-grey-2;
          border-radius: 0;
          color: $color-white;
          font-weight: normal;
          padding: 10px 20px;
          text-shadow: none;

          &:hover,
          &:focus {
            background-color: inherit;
          }
        }

        li + li a,
        li + li span.tbm-no-link {
          margin-bottom: 0;
        }
      }

      .btn {
        padding: 4px 10px;
        font-weight: normal;
        border-radius: 4px;
      }

      .open {
        > .tbm-submenu {
          display: block;
        }
      }
    }

    .tbm-column-inner {
      padding: 10px 20px;
    }

    .tbm-submenu {
      width: 100% !important;
      min-width: 100% !important;
      left: 0 !important;
      margin-left: 0 !important;
      transform: none !important;
    }

    .tbm-subnav {
      > li {
        a,
        span.tbm-no-link {
          padding: 5px 0;
          margin-left: 20px;
        }
      }
    }

    .tbm-block {
      margin-bottom: 0;

      .block-title {
        color: $color-white;
      }
    }
  }
}

// Animations, for desktop only.
@media (min-width: 980px) {
  .tbm.animate {
    .tbm-item {
      > .tbm-submenu {
        transition: all 400ms;
        backface-visibility: hidden;
        opacity: 0;
      }
    }

    .tbm-item.animating {
      > .tbm-submenu {
        display: block !important;
      }
    }

    .tbm-item.open {
      > .tbm-submenu {
        opacity: 1;
      }
    }

    .tbm-item.animating.open {
      > .tbm-submenu {
        opacity: 1;
      }
    }
  }

  .tbm.animate.zoom {
    .tbm-item {
      > .tbm-submenu {
        transform: scale(0, 0);
        transform-origin: 20% 20%;
      }
    }

    .tbm-item.open {
      > .tbm-submenu {
        transform: scale(1, 1);
      }
    }
  }

  .tbm.animate.elastic {
    .level0 {
      > .tbm-item {
        > .tbm-submenu {
          transform: scale(1, 0);
        }
      }
    }

    .tbm-item {
      > .tbm-submenu {
        transform: scale(0, 1);
        transform-origin: 10% 0;
      }
    }

    .tbm-item.open {
      > .tbm-submenu {
        transform: scale(1, 1);
      }
    }
  }

  .tbm.animate.slide {
    .tbm-item.animating {
      > .tbm-submenu {
        overflow: hidden;
      }
    }

    .tbm-item {
      > .tbm-submenu {
        > div {
          transition: all 400ms;
          backface-visibility: hidden;
          margin-top: -100%;
        }
      }

      .tbm-item {
        > .tbm-submenu {
          min-width: 0;

          > div {
            min-width: 200px;
            margin-top: 0;
            margin-left: -500px;
          }
        }
      }

      .tbm-item.open {
        > .tbm-submenu {
          > div {
            margin-left: 0;
          }
        }
      }
    }

    .tbm-item.open {
      > .tbm-submenu {
        > div {
          margin-top: 0%;
        }
      }
    }
  }
}
